body .authorization {
  width: 390px;
  min-height: 515px;
  background: #fff;
  border-radius: 5px;
}
body .authorization .typeOfAuthor {
  margin-bottom: 30px;
}
body .authorization .typeOfAuthor ul {
  display: flex;
  margin-bottom: 40px;
}
body .authorization .typeOfAuthor ul li {
  width: 50%;
}
body .authorization .typeOfAuthor ul li a {
  display: inline-block;
  width: 100%;
  height: 65px;
  font: 1.125em/65px "PTSans-Bold";
  color: #98a2a4;
  text-align: center;
  text-decoration: none;
  background-color: #ebebeb;
  border-radius: 5px;
}
body .authorization .typeOfAuthor ul li a:hover {
  outline: none;
}
body .authorization .author,
body .authorization .regist {
  text-align: center;
}
body .authorization .author form,
body .authorization .regist form {
  position: relative;
}
body .authorization .author form p,
body .authorization .regist form p {
  position: relative;
  margin: 10px 0;
}
body .authorization .author form p .user,
body .authorization .regist form p .user,
body .authorization .author form p .shield,
body .authorization .regist form p .shield,
body .authorization .author form p .phone,
body .authorization .regist form p .phone,
body .authorization .author form p .envelope,
body .authorization .regist form p .envelope {
  position: absolute;
  top: 15px;
  left: 85px;
  color: #00929e;
}
body .authorization .author form p input,
body .authorization .regist form p input {
  width: 150px;
  height: 40px;
  padding: 0 50px;
  font: 1em/40px "PTSans-Regular";
  color: #828282;
  overflow: hidden;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
body .authorization .author form p select,
body .authorization .regist form p select {
  width: 250px;
  height: 40px;
  padding-left: 25px;
  font: 1em/40px "PTSans-Regular";
  color: #828282;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
body .authorization .author form p .asterisk,
body .authorization .regist form p .asterisk {
  position: absolute;
  top: 20px;
  right: 85px;
  font-size: .35em;
  color: #ff0000;
}
body .authorization .author form p label .checkbox,
body .authorization .regist form p label .checkbox {
  display: none;
}
body .authorization .author form p label .checkbox-custom,
body .authorization .regist form p label .checkbox-custom {
  position: relative;
  width: 20px;
  height: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .authorization .author form p label .checkbox-custom,
body .authorization .regist form p label .checkbox-custom,
body .authorization .author form p label .label,
body .authorization .regist form p label .label {
  display: inline-block;
  vertical-align: middle;
}
body .authorization .author form p label .checkbox:checked + .checkbox-custom:before,
body .authorization .regist form p label .checkbox:checked + .checkbox-custom:before {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  background-color: #00929e;
  border-radius: 5px;
}
body .authorization .author form p .error,
body .authorization .regist form p .error {
  display: none;
  background-color: #ffd6d6;
}
body .authorization .author button,
body .authorization .regist button {
  margin: 30px 0;
  width: 250px;
  height: 40px;
  font: 1em/1.125em "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00929e;
  border: 1px solid #00929e;
  border-radius: 5px;
  outline: none;
}
body .authorization .author button:hover,
body .authorization .regist button:hover {
  cursor: pointer;
}
body .authorization .author a,
body .authorization .regist a {
  display: block;
  font: 0.875em/1em "PTSans-Regular";
  color: #828282;
}
body .authorization .ui-tabs-active .ui-tabs-anchor {
  color: #00929e !important;
  background-color: #fff !important;
  outline: none;
}
body .authorization .socialAuthor {
  padding: 0 0 25px;
  text-align: center;
}
body .authorization .socialAuthor p {
  margin: 25px 0;
  font: 1em/1.125em "PTSans-Bold";
  color: #00929e;
}
body .authorization .socialAuthor ul {
  display: flex;
  justify-content: center;
}
body .authorization .socialAuthor ul li a {
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 40px;
  font: 1em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
}
body .authorization .socialAuthor ul li a i {
  font-size: 1.5em;
}
body .authorization .socialAuthor ul li .facebook {
  background-color: #3b5998;
  border-bottom: 1px solid #293e6a;
}
body .authorization .socialAuthor ul li .gPlus {
  background-color: #db4437;
  border-bottom: 1px solid #8a251d;
}
body .authorization .close {
  display: none;
}
body .authorization .close p {
  display: flex;
  justify-content: center;
  align-items: center;
  font: 1em/50px "PTSans-Regular";
  color: #fff;
  background: #00929e;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
body .authorization .close p:hover {
  cursor: pointer;
}
body #authorizationConfirm {
  margin: 0 auto;
  padding: 15px 0;
  width: 475px;
  min-height: 100px;
  position: relative;
  text-align: center;
  background: #fff;
  border-radius: 5px;
}
body #authorizationConfirm p {
  margin: 25px 0;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #00929e;
}
body #authorizationConfirm span {
  display: block;
  font: 1em/1.125em "PTSans-Regular";
  color: #474747;
}
body #authorizationConfirm button {
  margin: 40px 0;
  width: 195px;
  height: 40px;
  font: 0.875em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00929e;
  border: none;
  border-radius: 5px;
  outline: none;
}
body #authorizationConfirm button:hover {
  cursor: pointer;
}
